<template>
  <div class="center">
    <shape-input @shapesAdded="shapesAdded($event)"/>
    <br/>
    <shape-svg-element :shapes="shapes"/>
  </div>
</template>

<script>
import ShapeInput from "@/components/ShapeInput.vue";
import ShapeSvgElement from "@/components/ShapeSvgElement.vue";

export default {
  name: "PlotterView",
  components: {
    ShapeInput,
    ShapeSvgElement
  },
  data() {
    return {
      shapes: []
    }
  },
  methods: {
    shapesAdded(e) {
      this.shapes = e;
    }     
  },
};
</script>

