const rectangleGenerator = (shapeData) => {
  var shape = {
    type: "rectangle",
    x: shapeData[0],
    y: shapeData[1],
    width: shapeData[2],
    height: shapeData[3],
    colour: randomColour(),
  };

  return shape;
};

const circleGenerator = (shapeData) => {
  var shape = {
    type: "circle",
    cx: shapeData[0],
    cy: shapeData[1],
    radius: shapeData[2],
    colour: randomColour(),
  };

  return shape;
};

const polygonGenerator = (shapeData) => {
  var points = "";
  for (let sd of shapeData) points += sd + " ";

  var shape = {
    type: "polygon",
    points: points,
    colour: randomColour(),
  };

  return shape;
};

const doughnutGenerator = (shapeData) => {
  var shape = {
    type: "doughnut",
    cx: shapeData[0],
    cy: shapeData[1],
    radius: shapeData[2],
    holeRadius: shapeData[3],
    colour: randomColour(),
  };

  return shape;
};

const randomColour = () => {
  var letters = "0123456789ABCDEF";
  var color = "#";
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
};

const factory = (shapeType) => {
  if (shapeType.toUpperCase() === "R") return rectangleGenerator;
  if (shapeType.toUpperCase() === "C") return circleGenerator;
  if (shapeType.toUpperCase() === "P") return polygonGenerator;
  if (shapeType.toUpperCase() === "D") return doughnutGenerator;
};

export const shapeGenerator = {
  factory,
};
