const rectangleValidator = (shapeData) => {
  var output = { valid: true, messages: [] };
  //rectangle requires 4 entries.
  //X, Y, width, height
  if (shapeData.length != 4) {
    output.valid = false;
    output.messages.push(
      `Rectangle requires 4 elements. ${shapeData.length} were supplied.`
    );
    return output;
  }

  for (let index in shapeData) {
    var el = parseInt(shapeData[index]);

    //check element is a number
    if (isNaN(el)) {
      output.valid = false;
      output.messages.push(`Element ${parseInt(index) + 1} is not a number.`);
    }

    //check element is not a negative
    if (el < 0) {
      output.valid = false;
      output.messages.push(
        `Element ${parseInt(index) + 1} cannot be less than zero.`
      );
    }
  }

  if (parseInt(shapeData[0]) + parseInt(shapeData[2]) > 250) {
    output.valid = false;
    output.messages.push(`Rectangle is out of range on the x axis.`);
  }

  if (parseInt(shapeData[1]) + parseInt(shapeData[3]) > 250) {
    output.valid = false;
    output.messages.push(`Rectangle is out of range on the y axis.`);
  }

  return output;
};

const circleValidator = (shapeData) => {
  var output = { valid: true, messages: [] };
  //rectangle requires 3 entries.
  //X, Y, radius
  if (shapeData.length != 3) {
    output.valid = false;
    output.messages.push(
      `Circle requires 3 elements.  ${shapeData.length} were supplied.`
    );
    return output;
  }

  for (let index in shapeData) {
    var el = parseInt(shapeData[index]);

    //check element is a number
    if (isNaN(el)) {
      output.valid = false;
      output.messages.push(`Element ${parseInt(index) + 1} is not a number.`);
    }

    //check element is not a negative
    if (el < 0) {
      output.valid = false;
      output.messages.push(
        `Element ${parseInt(index) + 1} cannot be less than zero.`
      );
    }
  }

  if (
    parseInt(shapeData[0]) + parseInt(shapeData[2]) > 250 ||
    parseInt(shapeData[2]) > parseInt(shapeData[0]) //if radius is larger than cx
  ) {
    output.valid = false;
    output.messages.push(`Circle is out of range on the x axis.`);
  }

  if (
    parseInt(shapeData[1]) + parseInt(shapeData[2]) > 250 ||
    parseInt(shapeData[2]) > parseInt(shapeData[1]) //if radius is larger than cy
  ) {
    output.valid = false;
    output.messages.push(`Circle is out of range on the y axis.`);
  }

  return output;
};

const polygonValidator = (shapeData) => {
  var output = { valid: true, messages: [] };

  for (let index in shapeData) {
    //check for too many commas
    if (shapeData[index].split(",").length > 2) {
      output.valid = false;
      output.messages.push(
        `Element ${parseInt(index) + 1} has too many commas.`
      );
    }

    //check for no comma
    if (shapeData[index].split(",").length < 2) {
      output.valid = false;
      output.messages.push(`Element ${parseInt(index) + 1} is missing a comma`);
    }

    for (let coord of shapeData[index].split(",")) {
      var el = parseInt(coord);

      //check element is a number
      if (isNaN(el)) {
        output.valid = false;
        output.messages.push(`Element ${parseInt(index) + 1} is not a number.`);
      }

      //check out of range
      if (el > 250 || el < 0) {
        output.valid = false;
        output.messages.push(`Element ${parseInt(index) + 1} is out of range.`);
      }
    }
  }

  return output;
};

const doughnutValidator = (shapeData) => {
  var output = { valid: true, messages: [] };
  //rectangle requires 3 entries.
  //X, Y, radius
  if (shapeData.length != 4) {
    output.valid = false;
    output.messages.push(
      `Doughnut requires 4 elements.  ${shapeData.length} were supplied.`
    );
    return output;
  }

  for (let index in shapeData) {
    var el = parseInt(shapeData[index]);

    //check element is a number
    if (isNaN(el)) {
      output.valid = false;
      output.messages.push(`Element ${parseInt(index) + 1} is not a number.`);
    }

    //check element is not a negative
    if (el < 0) {
      output.valid = false;
      output.messages.push(
        `Element ${parseInt(index) + 1} cannot be less than zero.`
      );
    }
  }

  if (
    parseInt(shapeData[0]) + parseInt(shapeData[2]) > 250 ||
    parseInt(shapeData[2]) > parseInt(shapeData[0]) //if radius is larger than cx
  ) {
    output.valid = false;
    output.messages.push(`Doughnut is out of range on the x axis.`);
  }

  if (
    parseInt(shapeData[1]) + parseInt(shapeData[2]) > 250 ||
    parseInt(shapeData[2]) > parseInt(shapeData[1]) //if radius is larger than cy
  ) {
    output.valid = false;
    output.messages.push(`Doughnut is out of range on the y axis.`);
  }

  //if outer radius is less than inner radius 
  if (parseInt(shapeData[2]) <= parseInt(shapeData[3])) {
    output.valid = false;
    output.messages.push(`Doughnut outer radius needs to be larger than the inner radius.`);
  }

  return output;
};

const noShapeValidator = () => {
  return { valid: false, messages: [`Shape type doesnt exist`] };
};

const factory = (shapeType) => {
  if (shapeType.toUpperCase() === "R") return rectangleValidator;
  if (shapeType.toUpperCase() === "C") return circleValidator;
  if (shapeType.toUpperCase() === "P") return polygonValidator;
  if (shapeType.toUpperCase() === "D") return doughnutValidator;
  return noShapeValidator;
};

export const shapeValidator = {
  factory,
};
