<template>
  <div  style="text-align: center">
    <svg class="shape-svg" width="250" height="250">
      <!-- RECTANGLE -->
      <rect
        v-for="(rectangle, index) in rectangles"
        :key="'r' + index"
        :x="rectangle.x"
        :y="rectangle.y"
        :width="rectangle.width"
        :height="rectangle.height"
        :fill="rectangle.colour"
      />

      <!-- CIRCLE -->
      <circle
        v-for="(circle, index) in circles"
        :key="'c' + index"
        :cx="circle.cx"
        :cy="circle.cy"
        :r="circle.radius"
        :fill="circle.colour"
      />

      <!-- POLYGON -->
      <polygon
        v-for="(polygon, index) in polygons"
        :key="'p' + index"
        :points="polygon.points"
        :fill="polygon.colour"
      />

      <!-- DOUGHNUT -->
      <circle
        v-for="(doughnut, index) in doughnuts"
        :key="'d' + index"
        :cx="doughnut.cx"
        :cy="doughnut.cy"
        :r="doughnut.radius"
        :fill="doughnut.colour"
      />
      <circle
        v-for="(doughnut, index) in doughnuts"
        :key="'di' + index"
        :cx="doughnut.cx"
        :cy="doughnut.cy"
        :r="doughnut.holeRadius"
        :fill="'white'"
      />
    </svg>
  </div>
</template>

<script>
export default {
  name: "ShapeSvgElement",
  props: {
    shapes: Array,
  },
  computed: {
    rectangles() {
      return this.shapes.filter((shape) => shape.type == "rectangle");
    },
    circles() {
      return this.shapes.filter((shape) => shape.type == "circle");
    },
    polygons() {
      return this.shapes.filter((shape) => shape.type == "polygon");
    },
    doughnuts() {
      return this.shapes.filter((shape) => shape.type == "doughnut");
    },
  },
};
</script>

<style scoped>
.shape-svg {
  border: 1px solid black;
}
</style>
