<template>
  <div style="text-align: center">
    <textarea
      class="shape-text-area"
      rows="4"
      cols="50"
      v-model="rawShapeData"
      style="width:394px"
    />
    <div class="error-text" v-for="(err, index) in errorMessages" :key="index">
      {{ err }}
    </div>
    <br />
    <div>
      <button-plotter buttonText="Add Shapes" @clicked="addShapes()" />
      <button-plotter
        buttonText="Clear Shapes"
        style="padding-left:20px"
        @clicked="clearShapes()"
      />
    </div>
  </div>
</template>

<script>
import { shapeValidator } from "@/classes/ShapeValidator";
import { shapeGenerator } from "@/classes/ShapeGenerator";

import ButtonPlotter from "@/components/ButtonPlotter";

export default {
  name: "ShapeInput",
  components: {
    ButtonPlotter,
  },
  data() {
    return {
      rawShapeData: "p 200,10 250,190 160,210\nc 20 100 20\nr 100 50 25 25",
      errorMessages: [],
      shapes: [],
      test: []
    };
  },
  methods: {
    clearShapes() {
      this.rawShapeData = "";
      this.$emit("shapesAdded", []);
    },
    addShapes() {
      this.errorMessages = [];

      //success of method, if one validation fails then do not process shapes
      let success = true;

      //loop through each line of the text area and validate the shape.
      let shapeData = this.rawShapeData.split("\n");    

      for (let index in shapeData) {
        //validate line
        let validationResults = this.shapeLineValidator(shapeData[index]);

        //if line is not valid then display error messages and set success to failed
        if (validationResults.valid === false) {
          success = false;
          this.errorMessages.push(`Line ${parseInt(index) + 1} failed:`);
          for (let mess of validationResults.messages) {
            this.errorMessages.push(`... ${mess}`);
          }
        }
      }

      //if all lines are valid
      if (success) {
        this.shapes = [];
        for (let shapeLine of shapeData) {
          const [shapeType, ...shapeData] = shapeLine.split(" ");

          const gen = shapeGenerator.factory(shapeType);
          this.shapes.push(gen(shapeData));
        }

        this.$emit("shapesAdded", this.shapes);
      }
    },
    shapeLineValidator(shapeLine) {
      //split string by space, first element being the shape type
      const [shapeType, ...shapeData] = shapeLine.split(" ");

      //request relevent shape validator from factory
      const val = shapeValidator.factory(shapeType);
      return val(shapeData);
    },
  },
};
</script>

<style scoped>
.shape-text-area {
  resize: none;
}

.error-text {
  font-size: 12px;
  color: red;
  text-align: left;
}
</style>
