<template>
  <span>
    <button class="button" @click="buttonClicked()">{{buttonText}}</button>
  </span>
</template>

<script>
export default {
  name: "ButtonPlotter",
  props: {
      buttonText: String
  },
  methods: {
    buttonClicked() {
      this.$emit("clicked", []);
    },
  },
};
</script>

<style scoped>
.button {
  color: #f4f4f4;
  background: #1abc9c;
  font-weight: bold;
  font-family: Arial, sans-serif;
  border-radius: 4px;
  border-bottom: 2px solid #3a9a95;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  padding: 10px 25px 10px 25px;
}

.button:active,
.button:focus {
  outline: none;
}
.button:hover {
  background: #2a8a85;
}
</style>
